<template>
    <div class="weui-tab">
        <div class="layout-header">
            <i class="iconfont icon-jiantou-copy-copy-copy head-back" @click="back()"></i>
            <p class="header-title">选择时间</p>
        </div>
        <p class="time-info"><i class="iconfont icon-info icon-time-info"></i>请选择开始时间和结束时间，至少一小时</p>
        <div class="time-select-container">
            <div class="time-select-every">
                <p class="select-time-font">选择日期</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" @click="getDate" readonly v-model="selectDay">
            </div>
            <div class="time-select-every">
                <p class="select-time-font">选择开始时间</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" @click="getStartTime" readonly v-model="selectStime">
            </div>
            <div class="time-select-every">
                <p class="select-time-font">选择结束时间</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" readonly v-model="selectEtime" @click="getEndTime">
            </div>
            <div class="time-select-every">
                <p class="active-time">{{selectTime}}</p>
            </div>
        </div>
        <router-link to="/soccer">
            <div class="weui-btn weui-btn_primary select-place-btn">去选场地</div>
        </router-link>
    
    </div>
</template>

<script>
    export default {
        data() {
            return {
                year: "",
                mounth: "",
                day: "",
                selectDay: "",
                selectStime: "16:00",
                selectEtime: "21:30",
                selectTime: ''
            };
        },
        mounted() {
            let myDate = new Date();
            this.year = myDate.getFullYear();
            this.mounth = myDate.getMonth() + 1;
            this.day = myDate.getDate();
            this.selectDay = this.year + "/" + this.mounth + "/" + this.day;
            this.selectTime = this.selectDay + " " + this.selectStime + "-" + this.selectEtime
        },
        methods: {
            getDate() {
                weui.datePicker({
                    start: new Date(), // 从今天开始
                    end: 2018,
                    defaultValue: [this.year, this.mounth, this.day],
                    onConfirm: result => {
                        this.selectDay = result[0].value + "/" + result[1].value + "/" + result[2].value;
                        this.selectTime = this.selectDay + " " + this.selectStime + "-" + this.selectEtime
                    },
                    id: "datePicker"
                });
            },
            getStartTime() {
                weui.picker([{
                        label: '16',
                        value: '16'
                    }, {
                        label: '17',
                        value: '17'
                    }, {
                        label: '18',
                        value: '18'
                    },
                    {
                        label: '19',
                        value: '19'
                    }, {
                        label: '20',
                        value: '20'
                    }, {
                        label: '21',
                        value: '21'
                    }
                ], [{
                    label: '00',
                    value: '00'
                }, {
                    label: '30',
                    value: '30'
                }], {
                    defaultValue: ['16', '00'],
                    onConfirm: result => {
                        this.selectStime = result[0].value + ":" + result[1].value;
                        this.selectTime = this.selectDay + " " + this.selectStime + "-" + this.selectEtime
                    },
                    id: 'startTime'
                });
            },
            getEndTime() {
                weui.picker([{
                        label: '16',
                        value: '16'
                    }, {
                        label: '17',
                        value: '17'
                    }, {
                        label: '18',
                        value: '18'
                    },
                    {
                        label: '19',
                        value: '19'
                    }, {
                        label: '20',
                        value: '20'
                    }, {
                        label: '21',
                        value: '21'
                    }
                ], [{
                    label: '00',
                    value: '00'
                }, {
                    label: '30',
                    value: '30'
                }], {
                    defaultValue: ['21', '30'],
                    onConfirm: result => {
                        this.selectEtime = result[0].value + ":" + result[1].value;
                        this.selectTime = this.selectDay + " " + this.selectStime + "-" + this.selectEtime
                    },
                    id: 'endTime'
                });
            },
            back() {
                //ajax请求
                this.$router.back();
            }
        }
    };
</script>

<style>
    .weui-picker__bd {
        font-size: 16px;
    }
    
    .time-info {
        font-size: 14px;
        padding: 20px 30px;
        color: #888;
    }
    
    .icon-time-info {
        padding-right: 10px;
    }
    
    .time-select-container {
        width: 100%;
        height: 400px;
    }
    
    .time-select-every {
        padding: 10px 56px;
        height: 80px;
    }
    
    .select-date-input {
        margin-top: 10px;
        width: 100%;
        height: 40px;
        outline: none;
        border: 1px solid #ccc;
        font-size: 15px;
        padding-left: 10px;
        border-radius: 5px;
    }
    
    .active-time {
        font-size: 14px;
        color: #259b24;
    }
    
    .select-time-font {
        font-size: 14px;
    }
    
    .select-place-btn {
        position: absolute;
        bottom: 7px;
        left: 7px;
        width: 96%;
        font-size: 16px;
        background: #259b24;
    }
</style>



