<template>
    <div class="weui-tab">
        <div class="weui-tab__panel">
            <div class="layout-header">
                <p class="order-header-title">我的</p>
            </div>
            <div class="us-wrap">
                <div class="myself-info">
                    <img src="../../assets/img/about.png" class="myself-img" />
                    <p class="myself-name">马嫒</p>
                </div>
            </div>
            <div class="page navbar js_show">
                <div class="weui-navbar-my">
                    <div class="weui-navbar__item" @click="weekExercise()">
                        周运动
                        <span class="navbar-active" v-show="dispalyExercise"></span>
                    </div>
                    <div class="weui-navbar__item" @click="weekConsume()">
                        周消费
                        <span class="navbar-active" v-show="dispalyConsume"></span>
                    </div>
                </div>
                <div id="week-consume"></div>
            </div>
            <Tabbar value="us"></Tabbar>
        </div>
    </div>
</template>

<script>
    import tabbar from "../../coms/tabbar";
    
    export default {
    
        data() {
            return {
                dispalyExercise: true,
                dispalyConsume: false
            };
        },
        components: {
            Tabbar: tabbar
        },
        mounted() {
            this.drawExercise();
        },
        methods: {
            weekExercise() {
                this.dispalyExercise = true;
                this.dispalyConsume = false;
                this.drawExercise();
            },
            weekConsume() {
                this.dispalyExercise = false;
                this.dispalyConsume = true;
                this.drawConsume();
            },
            drawConsume() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = echarts.init(document.getElementById("week-consume"));
                // 绘制图表
                myChart.clear();
                myChart.setOption({
                    title: {
                        text: "本周消费",
                        //   subtext: "纯属虚构",
                        x: "center"
                    },
                    tooltip: {
                        trigger: "item",
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: "vertical",
                        left: "left",
                        data: ["乒乓球", "健身房", "体操室", "其他", "羽毛球"]
                    },
                    series: [{
                        name: "访问来源",
                        type: "pie",
                        radius: "55%",
                        center: ["50%", "60%"],
                        data: [{
                                value: 335,
                                name: "乒乓球"
                            },
                            {
                                value: 310,
                                name: "健身房"
                            },
                            {
                                value: 234,
                                name: "体操室"
                            },
                            {
                                value: 135,
                                name: "其他"
                            },
                            {
                                value: 1548,
                                name: "羽毛球"
                            }
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: "rgba(0, 0, 0, 0.5)"
                            }
                        }
                    }]
                });
            },
            drawExercise() {
                // 绘制图表
                let myChart = echarts.init(document.getElementById("week-consume"));
                myChart.setOption({
                    title: {
                        text: "本周运动",
                        //   subtext: "纯属虚构",
                        x: "center"
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'none'
                        },
                        formatter: function(params) {
                            return params[0].name + ': ' + params[0].value;
                        }
                    },
                    xAxis: {
                        data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        },
                        axisLabel: {
                            textStyle: {
                                color: '#1aad19'
                            }
                        }
                    },
                    yAxis: {
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        },
                        axisLabel: {
                            show: false
                        }
                    },
                    color: ['#1aad19'],
                    series: [{
                        name: 'hill',
                        type: 'pictorialBar',
                        barCategoryGap: '-130%',
                        // symbol: 'path://M0,10 L10,10 L5,0 L0,10 z',
                        symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
                        itemStyle: {
                            normal: {
                                opacity: 0.5
                            },
                            emphasis: {
                                opacity: 1
                            }
                        },
                        data: [123, 60, 25, 18, 12, 2, 30],
                        z: 10
                    }, {
                        name: 'glyph',
                        type: 'pictorialBar',
                        barGap: '-100%',
                        // symbolPosition: 'end',
                        // symbolSize: 50,
                        // symbolOffset: [0, '-120%'],
                        data: [{
                            value: 123,
                            symbolSize: [0, 0]
                        }, {
                            value: 60,
                            symbolSize: [0, 0]
                        }, {
                            value: 25,
                            symbolSize: [0, 0]
                        }, {
                            value: 18,
                            symbolSize: [0, 0]
                        }, {
                            value: 12,
                            symbolSize: [0, 0]
                        }, {
                            value: 2,
                            symbolSize: [0, 0]
                        }, {
                            value: 30,
                            symbolSize: [0, 0]
                        }]
                    }]
                });
            }
        }
    };
</script>

<style>
    .el-tabs--border-card>.el-tabs__content {
        width: 100%;
        height: 100%;
        display: block !important;
    }
    
    .us-wrap {
        height: 30%;
        background: #fbfbfb;
        width: 100%;
        border-bottom: 1px solid #ccc;
        position: relative;
        top: 48px;
    }
    
    .myself-info {
        position: relative;
        top: 18%;
        width: 80px;
        height: 120px;
        margin: 0px auto;
    }
    
    .myself-img {
        width: 80px;
        height: 80px;
        border-radius: 50%;
    }
    
    .myself-name {
        padding-top: 10px;
        font-size: 16px;
        text-align: center;
    }
    
    .weui-navbar-my {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        position: relative;
        z-index: 500;
        top: 0;
        width: 100%;
        background-color: #fbfbfb;
    }
    
    #week-consume {
        margin-top: 5%;
        width: 350px;
        height: 300px;
        padding: 0px 10px;
    }
    
    .page__bd {
        background: #ffffff;
    }
</style>




