Vue.use(VueRouter)

const routes = [{
    path: '/home',
    component: require('./home')
}, {
    path: '/my',
    component: require('./my')
}, {
    path: '/order',
    component: require('./order')
}, {
    path: '/soccer',
    component: require('./soccer')
},{
    path: '/time',
    component: require('./select_time')
},{
    path: '/pay_success',
    component: require('./pay_success')
},{
    path: '/',
    redirect: '/home'
}]

const router = new VueRouter({
    routes
})

Vue.filter('time', val => {
    if (!val) return ''
    return moment(val).format('YYYY-MM-DD HH:mm')
})

const app = new Vue({
    el: '#app',
    router: router,
    components: {
        App: require('./app')
    },
    template: '<app></app>'
})

window.apiready = function() {
    api.addEventListener({
        name: 'keyback'
    }, function(ret, err) {
        var hashs = ['#/home', '#/me', '#/login']
        if (hashs.indexOf(window.location.hash) > -1) {
            api.closeWidget()
        } else {
            history.back()
        }
    })
}


