<template>
    <div class="weui-tab">
        <div class="layout-header">
            <p class="order-header-title">订单</p>
        </div>
        <div class="layout-body">
            <div class="weui-navbar">
                <div class="weui-navbar__item " @click="notUsedOrder">
                    未使用订单
                    <span class="navbar-active" v-show="dispalyBefore"></span>
                </div>
                <div class="weui-navbar__item" @click="historyOrder">
                    历史订单
                    <span class="navbar-active" v-show="dispalyHistory"></span>
                </div>
            </div>
            <div class="">
                <div v-show="dispalyBefore" class="disuse-order">
                    <div class="weui-media-box weui-media-box_appmsg" v-for="(item,index) in notUseOrderList" :key="item.id">
                        <div class="weui-media-box__hd">
                            <img class="weui-media-box__thumb" src="../../assets/img/about.png" alt="">
                        </div>
                        <div class="weui-media-box__bd">
                            <span class="order-stat-cancel" @click="cancleOrder(index,item)">退订</span>
                            <h4 class="weui-media-box__title">{{item.time}}</h4>
                            <p class="weui-media-box__desc">{{item.content}}</p>
                        </div>
                    </div>
                </div>
                <div v-show="dispalyHistory" class="history-order">
                    <div class="weui-media-box weui-media-box_appmsg history-order-item" v-for="item in historyOrderList" :key="item.id">
                        <div class="weui-media-box__hd">
                            <img class="weui-media-box__thumb" src="../../assets/img/dog.png" alt="">
                        </div>
                        <div class="weui-media-box__bd">
                            <span class="order-stat-used">已使用</span>
                            <h4 class="weui-media-box__title">{{item.time}}</h4>
                            <p class="weui-media-box__desc">{{item.content}}</p>
                        </div>
                    </div>
                </div>
            </div>
    
    
            <div id="dialog1" v-show="CancleDialog">
                <div class="weui-mask"></div>
                <div class="weui-dialog">
                    <div class="weui-dialog__hd"><strong class="weui-dialog__title">订单</strong></div>
                    <div class="weui-dialog__bd">取消 {{order.time}} {{order.content}} 的预定</div>
                    <div class="weui-dialog__ft">
                        <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_default" @click="CancleDialog=false">取消</a>
                        <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="confirmCancleOrder()">确定</a>
                    </div>
                </div>
            </div>
        </div>
        <Tabbar value="order"></Tabbar>
    </div>
</template>

<script>
    import tabbar from "../../coms/tabbar";
    export default {
        components: {
            Tabbar: tabbar
        },
        data() {
            return {
                order: {
                    time: "",
                    content: ""
                },
                cancelOrderIndex: 0,
                CancleDialog: false,
                dispalyBefore: true,
                dispalyHistory: false,
                notUseOrderList: [{
                        id: 0,
                        time: "2017/12/15 17:00-19:00",
                        content: "羽毛球3号，6号场地"
                    },
                    {
                        id: 1,
                        time: "2017/12/17 15:00-17:00",
                        content: "羽毛球11号，6号场地"
                    },
                    {
                        id: 2,
                        time: "2017/12/15 15:00-17:00",
                        content: "羽毛球9号场地"
                    }
                ],
                historyOrderList: [{
                        id: 0,
                        time: "2014/12/15 15:00-17:00",
                        content: "羽毛球3号，6号场地"
                    },
                    {
                        id: 0,
                        time: "2014/12/15 15:00-17:00",
                        content: "羽毛球3号，6号场地"
                    },
                    {
                        id: 0,
                        time: "2014/12/15 15:00-17:00",
                        content: "羽毛球3号，6号场地"
                    }
                ]
            };
        },
        methods: {
            notUsedOrder() {
                this.dispalyHistory = false;
                this.dispalyBefore = true;
            },
            historyOrder() {
                this.dispalyHistory = true;
                this.dispalyBefore = false;
            },
            cancleOrder(index, item) {
                this.order.time = item.time;
                this.order.content = item.content;
                this.CancleDialog = true;
                this.cancelOrderIndex = index;
            },
            confirmCancleOrder() {
                this.notUseOrderList.splice(this.cancelOrderIndex, 1);
                this.CancleDialog = false;
            }
        }
    };
</script>

<style>
    .history-order-item {
        background: #fbfbfb;
    }
    
    .order-stat-used {
        position: absolute;
        right: 2%;
        font-size: 14px;
        color: #666;
    }
    
    .order-stat-cancel {
        position: absolute;
        right: 4%;
        font-size: 14px;
        color: #1aad19;
    }
    
    .disuse-order {
        position: relative;
        height: 100%;
        background: #ffffff;
    }
    
    .history-order {
        height: 100%;
        background: #ffffff;
    }
</style>



